@import "abstract.scss";

.day-chart {
    height: 100%;
    position: relative;

    &__center {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 2rem;
    }
}

select::-ms-expand {
    display: none;
}
.date-selector {
    -o-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    position: absolute;
    top: 120px;
    right: 30px;
    z-index: 1;
    padding: 0.3em 0.5em;
    border-radius: 0.3em;
    background-color: $button-color;
    border: none;
    color: black;

    font-size: 1.5rem;
    cursor: pointer;

    &:focus {
        outline: none;
        font-weight: 600;
    }
    &:hover {
        font-weight: 600;
    }
    &::-webkit-scrollbar {
        display: none;
    }

    @media (max-width: 768px) {
        top: 190px;
    }

    option {
        margin-bottom: 0.15em;

        &:checked,
        &:hover {
            font-weight: 600;
        }
    }
}
