@import "abstract.scss";

.main-pg {
    @include flex-center;
    flex-direction: column;
    justify-content: center;
    height: calc(100vh - 70px);

    &__title {
        margin-bottom: 6em;

        font-family: "Lora", serif;

        p {
            font-size: 2rem;
            font-style: italic;
        }

        h1 {
            font-size: 5rem;
            font-weight: 600;
            margin-top: 0.4em;
        }
    }

    &__btns {
        button {
            font-size: 1.4rem;
            height: 3.5em;
            width: 3.5em;
            margin: 0 1em;
            border-radius: 10%;
        }
    }
}
